.common-controller-container {
    display: block;
}

.toggle-btn {
    font-size: 20px;
    padding: 5px;
    margin: 10px;
    border:1px solid gray;
    color: gray;
    cursor: pointer;
}

.toggle-btn:hover {
    background: gray;
    color: white;
    transition: .7s;
}

.toggle-btn.active {
    border-color:#333;
    background: #333;
    color: white;
}

.play-controller-container{
    margin-top: 1rem;
}

.play-controller {
    font-size: 30px;
    border: 1px solid gray;
    color: gray;
    padding: 10px;
    cursor: pointer;
}

.play-controller:hover {
    color: #222;
    border-color: #222;
    transition: .7s;
}

.slider-container {
    margin-top: 1rem;
    display: inline-block;
    width: 300px;
    margin-left: 5px;
    margin-right: 5px;
}