.simulator-container {
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select:none;
    user-select:none;
    -o-user-select:none;
}

#carom-simulator-canvas {
    width   : 100%;
    height  : 100%;
}

.view-selector {
    position: absolute;
    left: 5px;
    top: 12px;
}

.view-selector .view-selector-item {
    padding: 5px;
    border: 1px solid #aaa;
    color: white;
    cursor: pointer;
}

.view-selector .view-selector-item:hover {
    background: #888;
    color: #555;
}

.view-selector .view-selector-item.active {
    background: #aaa;
    color: #333;
}